"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Button } from "@/components/ui/button"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Textarea } from "@/components/ui/textarea"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Checkbox } from "@/components/ui/checkbox"

interface FormData {
  name: string
  age: string
  weight: string
  height: string
  activityLevel: string
  dietaryRestrictions: string
  goal: string
  gender: string
  location: string
  medicalConditions: string[]
  isUnderTreatment: boolean
  treatmentType?: string
  allergies: string
  bloodPressure: string
  bloodSugar: string
  medications: string
}

interface MedicalCondition {
  label: string
  value: string
  restrictions: string[]
  recommendations: string[]
}

const MEDICAL_CONDITIONS: MedicalCondition[] = [
  {
    label: "Diabetes",
    value: "diabetes",
    restrictions: ["Sugar", "Simple carbohydrates", "White rice"],
    recommendations: ["High-fiber foods", "Green vegetables", "Whole grains"]
  },
  {
    label: "Hypertension",
    value: "hypertension",
    restrictions: ["Salt", "Processed foods", "Caffeine"],
    recommendations: ["Low-sodium foods", "Leafy greens", "Berries"]
  },
  {
    label: "Cancer (Under Treatment)",
    value: "cancer",
    restrictions: ["Raw foods", "Undercooked meats", "Certain fruits"],
    recommendations: ["Protein-rich foods", "Cooked vegetables", "Small frequent meals"]
  },
  {
    label: "Heart Disease",
    value: "heartDisease",
    restrictions: ["Saturated fats", "Trans fats", "High-sodium foods"],
    recommendations: ["Omega-3 rich foods", "Lean proteins", "Whole grains"]
  },
  {
    label: "Kidney Disease",
    value: "kidneyDisease",
    restrictions: ["High-phosphorus foods", "High-potassium foods", "Salt"],
    recommendations: ["Low-protein foods", "Controlled portions", "Approved vegetables"]
  }
]

interface Meal {
  calories: number
  suggestions: string[]
}

interface Meals {
  breakfast: Meal
  lunch: Meal
  dinner: Meal
  snacks: Meal
}

interface Macros {
  protein: number
  carbs: number
  fats: number
}

interface DietPlan {
  calories: number
  meals: Meals
  macros: Macros
}

export default function DietGenerator() {
  const [formData, setFormData] = useState<FormData>({
    name: "",
    age: "",
    weight: "",
    height: "",
    activityLevel: "",
    dietaryRestrictions: "",
    goal: "",
    gender: "male",
    location: "Bareilly, Uttar Pradesh",
    medicalConditions: [],
    isUnderTreatment: false,
    treatmentType: "",
    allergies: "",
    bloodPressure: "",
    bloodSugar: "",
    medications: ""
  })

  const [loading, setLoading] = useState(false)
  const [dietPlan, setDietPlan] = useState<DietPlan | null>(null)
  const [error, setError] = useState("")

  const activityMultipliers: { [key: string]: number } = {
    sedentary: 1.2,
    light: 1.375,
    moderate: 1.55,
    active: 1.725,
    veryActive: 1.9
  }

  const calculateCalories = (): number => {
    const weight = parseFloat(formData.weight)
    const height = parseFloat(formData.height)
    const age = parseFloat(formData.age)
    
    // Basic BMR calculation using Harris-Benedict equation
    let bmr = 10 * weight + 6.25 * height - 5 * age
    bmr = formData.gender === "female" ? bmr - 161 : bmr + 5

    // Activity level multiplier
    const calories = bmr * (activityMultipliers[formData.activityLevel] || activityMultipliers.moderate)
    
    // Adjust based on goal
    switch(formData.goal) {
      case "lose":
        return Math.round(calories - 500)
      case "gain":
        return Math.round(calories + 500)
      default:
        return Math.round(calories)
    }
  }

  const generateMeals = (): Meals => {
    const calories = calculateCalories()
    
    // Get all restrictions based on medical conditions
    const restrictions: string[] = []
    const recommendations: string[] = []
    
    formData.medicalConditions.forEach(condition => {
      const medCondition = MEDICAL_CONDITIONS.find(mc => mc.value === condition)
      if (medCondition) {
        restrictions.push(...medCondition.restrictions)
        recommendations.push(...medCondition.recommendations)
      }
    })

    // Special considerations for patients under treatment
    if (formData.isUnderTreatment) {
      if (formData.treatmentType === "chemotherapy") {
        restrictions.push("Spicy foods", "Raw foods", "Citrus fruits")
        recommendations.push("Small frequent meals", "Cold foods", "Protein-rich foods")
      } else if (formData.treatmentType === "radiation") {
        restrictions.push("Hard to swallow foods", "Acidic foods")
        recommendations.push("Soft foods", "Room temperature foods", "High-calorie foods")
      }
    }

    // Adjust meal suggestions based on conditions
    const meals: Meals = {
      breakfast: {
        calories: Math.round(calories * 0.3),
        suggestions: formData.isUnderTreatment ? [
          "Soft cooked oatmeal with mashed banana",
          "Smooth yogurt with honey",
          "Warm vegetable soup with soft bread"
        ] : [
          "Oatmeal with berries and nuts (avoid nuts if allergic)",
          "Greek yogurt with honey and soft fruits",
          "Whole grain toast with avocado (if no digestive issues)"
        ]
      },
      lunch: {
        calories: Math.round(calories * 0.35),
        suggestions: formData.isUnderTreatment ? [
          "Steamed fish with soft vegetables",
          "Tender chicken soup with rice",
          "Soft cooked dal with rice"
        ] : [
          "Grilled chicken salad with light dressing",
          "Quinoa bowl with steamed vegetables",
          "Lentil soup with whole grain bread"
        ]
      },
      dinner: {
        calories: Math.round(calories * 0.25),
        suggestions: formData.isUnderTreatment ? [
          "Soft cooked rice with dal",
          "Mashed potatoes with tender chicken",
          "Vegetable khichdi"
        ] : [
          "Baked fish with roasted vegetables",
          "Lean chicken curry with rice",
          "Mixed vegetable curry with roti"
        ]
      },
      snacks: {
        calories: Math.round(calories * 0.1),
        suggestions: formData.isUnderTreatment ? [
          "Smooth fruit lassi",
          "Soft cooked khichdi",
          "Warm milk with turmeric"
        ] : [
          "Mixed nuts and dried fruits (if allowed)",
          "Fruit smoothie",
          "Roasted chana (if no digestive issues)"
        ]
      }
    }

    // Add dietary notes based on medical conditions
    const dietaryNotes = [
      ...new Set([
        ...restrictions.map(r => `Avoid: ${r}`),
        ...recommendations.map(r => `Recommended: ${r}`)
      ])
    ]

    return {
      ...meals,
      dietaryNotes
    }
  }

  const calculateMacros = (): Macros => {
    const calories = calculateCalories()
    return {
      protein: Math.round((calories * 0.3) / 4), // 30% protein
      carbs: Math.round((calories * 0.45) / 4),  // 45% carbs
      fats: Math.round((calories * 0.25) / 9)    // 25% fats
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError("")

    try {
      // Simulating API call
      await new Promise((resolve) => setTimeout(resolve, 1500))
      
      const generatedDiet: DietPlan = {
        calories: calculateCalories(),
        meals: generateMeals(),
        macros: calculateMacros(),
      }
      
      setDietPlan(generatedDiet)
    } catch (err) {
      setError("Failed to generate diet plan. Please try again.")
    } finally {
      setLoading(false)
    }
  }

  return (
    <main className="min-h-screen bg-gradient-to-b from-gray-50 to-white p-4 md:p-8">
      <div className="max-w-6xl mx-auto">
        <div className="text-center mb-12">
          <h1 className="text-5xl font-bold text-gray-900 mb-3 bg-clip-text text-transparent bg-gradient-to-r from-blue-600 to-cyan-600">Diet Generator</h1>
          <p className="text-gray-600 text-lg">Create your personalized diet plan in seconds</p>
        </div>

        <div className="grid md:grid-cols-2 gap-8">
          <Card className="p-6 shadow-lg hover:shadow-xl transition-shadow duration-300 border-0">
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="space-y-4">
                <div>
                  <Label htmlFor="name">Name</Label>
                  <Input
                    id="name"
                    value={formData.name}
                    onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                    placeholder="Enter your name"
                    required
                  />
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="age">Age</Label>
                    <Input
                      id="age"
                      type="number"
                      value={formData.age}
                      onChange={(e) => setFormData({ ...formData, age: e.target.value })}
                      placeholder="Years"
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="gender">Gender</Label>
                    <Select
                      value={formData.gender}
                      onValueChange={(value) => setFormData({ ...formData, gender: value })}
                    >
                      <SelectTrigger>
                        <SelectValue placeholder="Select gender" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="male">Male</SelectItem>
                        <SelectItem value="female">Female</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="weight">Weight (kg)</Label>
                    <Input
                      id="weight"
                      type="number"
                      value={formData.weight}
                      onChange={(e) => setFormData({ ...formData, weight: e.target.value })}
                      placeholder="Kilograms"
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="height">Height (cm)</Label>
                    <Input
                      id="height"
                      type="number"
                      value={formData.height}
                      onChange={(e) => setFormData({ ...formData, height: e.target.value })}
                      placeholder="Centimeters"
                      required
                    />
                  </div>
                </div>

                <div>
                  <Label htmlFor="activityLevel">Activity Level</Label>
                  <Select
                    value={formData.activityLevel}
                    onValueChange={(value) => setFormData({ ...formData, activityLevel: value })}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Select level" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="sedentary">Sedentary</SelectItem>
                      <SelectItem value="light">Lightly Active</SelectItem>
                      <SelectItem value="moderate">Moderately Active</SelectItem>
                      <SelectItem value="active">Very Active</SelectItem>
                      <SelectItem value="veryActive">Extra Active</SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                <div>
                  <Label htmlFor="goal">Goal</Label>
                  <Select
                    value={formData.goal}
                    onValueChange={(value) => setFormData({ ...formData, goal: value })}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Select goal" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="lose">Lose Weight</SelectItem>
                      <SelectItem value="maintain">Maintain Weight</SelectItem>
                      <SelectItem value="gain">Gain Weight</SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                <div className="space-y-4">
                  <div>
                    <Label>Medical Conditions</Label>
                    <div className="grid grid-cols-2 gap-2 mt-2">
                      {MEDICAL_CONDITIONS.map((condition) => (
                        <div key={condition.value} className="flex items-center space-x-2">
                          <Checkbox
                            id={condition.value}
                            checked={formData.medicalConditions.includes(condition.value)}
                            onCheckedChange={(checked: boolean) => {
                              setFormData({
                                ...formData,
                                medicalConditions: checked
                                  ? [...formData.medicalConditions, condition.value]
                                  : formData.medicalConditions.filter((c) => c !== condition.value)
                              })
                            }}
                          />
                          <Label htmlFor={condition.value} className="text-sm">
                            {condition.label}
                          </Label>
                        </div>
                      ))}
                    </div>
                  </div>

                  <div>
                    <Label htmlFor="isUnderTreatment">Currently Under Treatment</Label>
                    <div className="flex items-center space-x-2 mt-2">
                      <Checkbox
                        id="isUnderTreatment"
                        checked={formData.isUnderTreatment}
                        onCheckedChange={(checked: boolean) => 
                          setFormData({ ...formData, isUnderTreatment: checked })
                        }
                      />
                      <Label htmlFor="isUnderTreatment" className="text-sm">
                        Yes, I am under medical treatment
                      </Label>
                    </div>
                  </div>

                  {formData.isUnderTreatment && (
                    <div>
                      <Label htmlFor="treatmentType">Treatment Type</Label>
                      <Select
                        value={formData.treatmentType}
                        onValueChange={(value) => setFormData({ ...formData, treatmentType: value })}
                      >
                        <SelectTrigger>
                          <SelectValue placeholder="Select treatment type" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="chemotherapy">Chemotherapy</SelectItem>
                          <SelectItem value="radiation">Radiation Therapy</SelectItem>
                          <SelectItem value="surgery">Post Surgery Recovery</SelectItem>
                          <SelectItem value="other">Other Treatment</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                  )}

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="bloodPressure">Blood Pressure</Label>
                      <Input
                        id="bloodPressure"
                        value={formData.bloodPressure}
                        onChange={(e) => setFormData({ ...formData, bloodPressure: e.target.value })}
                        placeholder="e.g., 120/80"
                      />
                    </div>
                    <div>
                      <Label htmlFor="bloodSugar">Blood Sugar</Label>
                      <Input
                        id="bloodSugar"
                        value={formData.bloodSugar}
                        onChange={(e) => setFormData({ ...formData, bloodSugar: e.target.value })}
                        placeholder="e.g., 100 mg/dL"
                      />
                    </div>
                  </div>

                  <div>
                    <Label htmlFor="allergies">Allergies</Label>
                    <Textarea
                      id="allergies"
                      value={formData.allergies}
                      onChange={(e) => setFormData({ ...formData, allergies: e.target.value })}
                      placeholder="List any food allergies"
                      className="h-20"
                    />
                  </div>

                  <div>
                    <Label htmlFor="medications">Current Medications</Label>
                    <Textarea
                      id="medications"
                      value={formData.medications}
                      onChange={(e) => setFormData({ ...formData, medications: e.target.value })}
                      placeholder="List any medications you are currently taking"
                      className="h-20"
                    />
                  </div>

                  <div>
                    <Label htmlFor="restrictions">Dietary Restrictions</Label>
                    <Textarea
                      id="restrictions"
                      value={formData.dietaryRestrictions}
                      onChange={(e) => setFormData({ ...formData, dietaryRestrictions: e.target.value })}
                      placeholder="Enter any dietary restrictions or preferences"
                      className="h-20"
                    />
                  </div>
                </div>
              </div>

              <Button type="submit" className="w-full bg-gradient-to-r from-blue-600 to-cyan-600 hover:from-blue-700 hover:to-cyan-700" disabled={loading}>
                {loading ? "Generating..." : "Generate Diet Plan"}
              </Button>

              {error && (
                <Alert variant="destructive">
                  <AlertDescription>{error}</AlertDescription>
                </Alert>
              )}
            </form>
          </Card>

          {dietPlan && (
            <Card className="p-6 shadow-lg hover:shadow-xl transition-shadow duration-300 border-0">
              <Tabs defaultValue="overview">
                <TabsList className="w-full mb-4">
                  <TabsTrigger value="overview" className="flex-1">Overview</TabsTrigger>
                  <TabsTrigger value="meals" className="flex-1">Meal Plan</TabsTrigger>
                  <TabsTrigger value="macros" className="flex-1">Macros</TabsTrigger>
                </TabsList>

                <TabsContent value="overview">
                  <div className="space-y-4">
                    <h3 className="text-xl font-semibold">Daily Calories: {dietPlan.calories}</h3>
                    <div className="grid grid-cols-3 gap-4">
                      <Card className="p-4 text-center">
                        <p className="text-sm text-gray-600">Protein</p>
                        <p className="text-lg font-semibold">{dietPlan.macros.protein}g</p>
                      </Card>
                      <Card className="p-4 text-center">
                        <p className="text-sm text-gray-600">Carbs</p>
                        <p className="text-lg font-semibold">{dietPlan.macros.carbs}g</p>
                      </Card>
                      <Card className="p-4 text-center">
                        <p className="text-sm text-gray-600">Fats</p>
                        <p className="text-lg font-semibold">{dietPlan.macros.fats}g</p>
                      </Card>
                    </div>
                  </div>
                </TabsContent>

                <TabsContent value="meals">
                  <div className="space-y-6">
                    {Object.entries(dietPlan.meals).map(([meal, data]) => (
                      <div key={meal} className="space-y-2">
                        <h4 className="text-lg font-semibold capitalize">{meal} ({data.calories} cal)</h4>
                        <ul className="list-disc list-inside space-y-1">
                          {data.suggestions.map((suggestion: string, index: number) => (
                            <li key={index} className="text-gray-600">{suggestion}</li>
                          ))}
                        </ul>
                      </div>
                    ))}
                  </div>
                </TabsContent>

                <TabsContent value="macros">
                  <div className="space-y-4">
                    <h3 className="text-xl font-semibold mb-4">Detailed Macronutrients</h3>
                    <div className="space-y-6">
                      <div>
                        <h4 className="font-medium mb-2">Protein: {dietPlan.macros.protein}g</h4>
                        <div className="h-2 bg-gray-200 rounded-full">
                          <div
                            className="h-full bg-blue-500 rounded-full"
                            style={{ width: "30%" }}
                          />
                        </div>
                      </div>
                      <div>
                        <h4 className="font-medium mb-2">Carbs: {dietPlan.macros.carbs}g</h4>
                        <div className="h-2 bg-gray-200 rounded-full">
                          <div
                            className="h-full bg-green-500 rounded-full"
                            style={{ width: "45%" }}
                          />
                        </div>
                      </div>
                      <div>
                        <h4 className="font-medium mb-2">Fats: {dietPlan.macros.fats}g</h4>
                        <div className="h-2 bg-gray-200 rounded-full">
                          <div
                            className="h-full bg-yellow-500 rounded-full"
                            style={{ width: "25%" }}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </TabsContent>
              </Tabs>
            </Card>
          )}
        </div>
      </div>
    </main>
  )
}
